import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WidgetsModule } from 'src/app/widgets/chq-widgets.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatCardModule } from '@angular/material/card';
import { MatIconModule } from '@angular/material/icon';
import { MatBadgeModule } from '@angular/material/badge';
import { Route, RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatSidenavModule } from '@angular/material/sidenav';
import { WorkflowComponent } from './workflow/workflow.component';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { TranslateModule } from '@ngx-translate/core';
import { MatTooltipModule } from '@angular/material/tooltip';
import { DirectiveModule } from 'src/app/helper/directive.module';

const workflowRoutes: Route[] = [
  {
    path: '',
    component: WorkflowComponent,
    resolve: { version: VersionControlResolver }
  },
];

@NgModule({
  declarations: [
    WorkflowComponent
  ],
  imports: [
    CommonModule,
    WidgetsModule,
    SharedModule,
    MatDatepickerModule,
    MatCardModule,
    MatIconModule,
    MatBadgeModule, 
    RouterModule.forChild(workflowRoutes),
    FormsModule,
    ReactiveFormsModule,
    MatSidenavModule,
    TranslateModule,
    MatTooltipModule,
    DirectiveModule
  ]
})
export class WorkflowModule { }
